.sider-wrapper {
  text-align: center;
  border-right: 1px solid #ebedf0;
  height: calc(100vh-40px - 64px);
  overflow-y: auto;
}
.sider-wrapper h2 {
  margin-top: 20px;
  font-size: 1.5em;
  font-weight: 700;
}
.sider-wrapper .social {
  padding: 0 25px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.sider-wrapper .social li {
  padding: 0 5px;
}
.sider-wrapper .social li a {
  margin-left: 5px;
}
.sider-wrapper .social li:hover {
  background-color: #f0f2f5;
}
.sider-wrapper .hot {
  text-align: center;
  padding: 0 20px;
}
.sider-wrapper .hot li {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.sider-wrapper .hot li:hover {
  background-color: #f0f2f5;
}
.sider-wrapper .hot li:hover a {
  color: #1890ff;
}
.sider-wrapper .tag-group {
  padding: 0 20px;
}
.sider-wrapper .tag-group .ant-badge {
  margin: 5px;
}
