.header-container {
  padding: 0px;
  background-color: #fff;
  box-shadow: 0 2px 8px #f0f1f2;
  position: relative;
  z-index: 10;
}
.header-container .search-bar {
  float: left;
  margin-top: 20px;
}
.header-container .search-bar .search-container {
  border-left: 1px solid #ebedf0;
  padding-left: 15px;
  height: 22px;
  line-height: 22px;
}
.header-container .search-bar .search-container .anticon-search {
  position: relative;
  top: 0.5px;
  color: #ced4d9;
}
.header-container .search-bar .search-container .ant-input {
  height: 22px;
  border: none;
  box-shadow: none;
  width: 200px;
}
@media screen and (max-width: 738px) {
  .header-container .search-bar .search-container .ant-input {
    width: 80px;
  }
}
.header-container .logo {
  display: flex;
  align-content: center;
  justify-content: center;
}
.header-container .btn-group {
  float: right;
}
@media screen and (max-width: 738px) {
  .header-container .btn-group {
    margin-right: 5px !important;
  }
  .header-container .btn-group .ant-btn-primary {
    margin-right: 5px !important;
  }
}
.header-container .user-avatar {
  margin: 5px  20px 0px 20px;
  float: right;
}
@media screen and (max-width: 738px) {
  .header-container .user-avatar {
    margin: 5px  10px 0px 10px;
  }
}
.header-container .menu-bar {
  float: right;
  margin: 0 10px;
}
.header-container .menu-bar .ant-menu {
  height: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: none;
}
.header-container .phone-title {
  font-size: 20px;
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
.header-container .iconkeai {
  color: #1296db;
  font-size: 16px;
  margin-right: 10px;
}
